<script>
import FertilizationLineCreateEditDailog from "@/components/fertilization/line-create-edit-dialog.vue";

import { useFertilizationGridInit } from "@/composable/fertilization";
import {
    computed,
    inject,
    onMounted,
    onUnmounted,
    provide,
    watch,
    ref
} from "vue";
import { emitter } from "@/event-bus";
import { usePermissionCheck } from "@/composable/users";
import { FERTILIZATION_COLOR } from "@/config";
import { $axios } from "@/service/app-http";
import { useToast } from "primevue/usetoast";

export default {
    components: { FertilizationLineCreateEditDailog },
    setup() {
        const {
            store,
            fertilizationGrid,
            sortToggle,
            updateFertilization,
            gridDesc,
            createFetilizationRow,
            fertilizationGridDays,
            moveGridCell,
            openEmbryoDetailDialog,
            closeEmbryoDetailDialog,
            fillDaysRow,
            createFertilizationLineDialog,
            editFetilizationRow,
            ferilizationLine,
            showFateInGrid,
            onGridCellClick,
            selectedEmbryo,
            openPdfReport,
            checkAll,
            checkLine,
            deleteLines,
            editFetilizationRows,
            isFertilizationEdit,
            selectedLines
        } = useFertilizationGridInit();

        watch(fertilizationGrid, (newVal) => {
            checkLine.value = newVal.map((_) => false);
        });

        watch(checkAll, (newVal) => {
            if (newVal) {
                checkLine.value = checkLine.value.map((_) => true);
            } else {
                checkLine.value = checkLine.value.map((_) => false);
            }
        });

        const toast = useToast();

        const isChceked = computed(() => {
            return checkLine.value.filter((item) => item).length != 0;
        });

        const {
            hasUpdatePermission,
            hasCreatePermission
        } = usePermissionCheck();

        provide("createFertilizationLineDialog", createFertilizationLineDialog);
        provide("ferilizationLine", ferilizationLine);
        provide("isFertilizationEdit", isFertilizationEdit);
        provide("selectedLines", selectedLines);

        const updateExpToServer = (row) => {
            const payload = {
                lineId: row.lineId,
                exp: row.exp
            };

            $axios
                .post("fertilization/update-exp-fert-line", payload)
                .then((resp) => {
                    toast.add({
                        severity: "success",
                        summary: "Success",
                        detail: "Saved successfully",
                        life: 2000
                    });
                })
                .catch((err) => {});
        };

        const getFertilizationColor = (fate) => {
            if (fate && fate.trim().toLowerCase() === "frozen")
                return FERTILIZATION_COLOR.FROZEN;
            if (fate && fate.trim().toLowerCase() === "discard")
                return FERTILIZATION_COLOR.DISCARD;
            if (fate && fate.trim().toLowerCase() === "transfer")
                return FERTILIZATION_COLOR.TRANSFER;
            else return "#F6F6F6";
        };

        const aspirationForm = inject("aspirationForm");

        const split2Input = (input, index) => {
            console.log(input);

            if (input) {
                const parts = input && input.trim().split(/ (.+)/);
                return parts[index - 1] ? parts[index - 1] : "";
            }
        };

        const saveComment = (line) => {
            let fertilization = store.state.fertilization.fertilization;

            store
                .dispatch("fertilization/createFertilizationLine", {
                    ...line,
                    fertilizationId: fertilization.fertId
                })
                .then(() => {})
                .catch(() => {
                    console.log("err");
                })
                .finally(() => {
                    createFertilizationLineDialog.value = false;
                    loading.value = false;
                });
        };

        watch(() => store.state.fertilization.fertilization, fillDaysRow);

        onMounted(() => {
            store.commit("fertilization/EMPTY_FERTILIZAION_LINE");
            emitter.on("embyro-detail-dialog:move-cell", moveGridCell);
        });

        onUnmounted(() => {
            emitter.off("embyro-detail-dialog:move-cell", moveGridCell);
        });

        return {
            fertilizationGrid,
            updateExpToServer,
            sortToggle,
            gridDesc,
            updateFertilization,
            createFetilizationRow,
            fertilizationGridDays,
            openEmbryoDetailDialog,
            closeEmbryoDetailDialog,
            editFetilizationRow,
            showFateInGrid,
            onGridCellClick,
            selectedEmbryo,
            openPdfReport,
            hasUpdatePermission,
            hasCreatePermission,
            split2Input,
            getFertilizationColor,
            save: () => {
                emitter.emit("cycle-monitoring:aspiration-form-save");
            },
            saveComment,
            aspirationForm,
            checkAll,
            checkLine,
            deleteLines,
            editFetilizationRows,
            isChceked
        };
    }
};
</script>

<style scoped>
.p-grid {
    margin-top: 1px;
}
.p-col-fixed {
    margin-left: 1px;
    padding: 2px;
}

.myseparator {
