    font-weight: bold;
    font-size: 14px;
}
.p-calendar :deep() .p-inputtext[disabled] {
    background-color: transparent;
}
.p-avatar {
    background-color: transparent;
}
.fate-text {
    padding: 2px 8px 2px 8px;
    color: white;
    font-size: 12px;
    border-radius: 20px;
}
.selected-grid {
    background-color: aqua;
}
.frozen {
    background-color: aqua;
}
.discard {
    background-color: greenyellow;
}
.tranfer {
    background-color: red;
}

:deep() .p-scrollpanel-bar {
    background-color: #505253;
    opacity: 1;
    transition: background-color 0.3s;
}

.ferti-container {
    width: calc(100vw - 230px);
}

@media (max-width: 768px) {
    .ferti-container {
        width: 100vw;
    }
}

.comment {
    display: flex;
    gap: 10px;
    align-items: center;
}

.button-container {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    align-items: center;
}

.form-container {
    display: flex;
    gap: 20px;
    align-items: center;
    flex-wrap: nowrap;
}

@media (max-width: 768px) {
    .form-container {
        flex-direction: column;
        gap: 15px;
    }

    .comment {
        flex-direction: column;
        width: 100%;
    }

    .button-container {
        flex-direction: column;
        width: 100%;
    }

    .p-field {
        width: 100%;
    }

    .p-button {
        width: 100%;
    }
}
</style>
