<template>
    <div style="background-color: #fff;">
        <div class="flex-container">
            <div class="flex-item">
                <fertilization-foll-aspiration-form />
                <fertilization-grid v-show="showGrid" />
                <div
                    class="p-d-flex p-ai-center p-jc-center"
                    v-if="!showGrid"
                    style="height: 300px;"
                >
                    <p-inline-message
                        v-show="problem"
                        severity="warn"
                        style="width: 400px;"
                    >
                        Please select cycle first
                    </p-inline-message>
                </div>
            </div>
            <div
                class="fixed-width-item"
                :style="{ 'overflow-y': 'hidden', width: '200px' }"
            >
                <fertilization-tree-view class="hide-on-mobile" />
            </div>
        </div>
    </div>

    <template v-if="isBornCycle" class="p-inputtext-sm">
        <born-fresh-embryo />
        <bron-frozen-embryo />
        <!-- <bron-embyro-transfer /> -->
    </template>

    <fertilization-embryo-detail-dialog />
    <fertilization-embryo-detail-new-confirm-dialog />
</template>

<script>
import {
    onMounted,
    onUnmounted,
    provide,
    ref,
    reactive,
    watch,
    computed
} from "vue";
import { useFertilizationInit } from "@/composable/fertilization";
import { emitter } from "@/event-bus";

import FertilizationGrid from "@/components/fertilization/grid.vue";
import FertilizationEmbryoDetailDialog from "@/components/fertilization/embryo-detail-dialog.vue";
import FertilizationTreeView from "@/components/fertilization/tree-view.vue";
import FertilizationCreateForm from "@/components/fertilization/create-form.vue";
import FertilizationFollAspirationForm from "@/components/fertilization/foll-aspiration-form.vue";
import FertilizationEmbryoDetailNewConfirmDialog from "@/components/fertilization/embryo-detail-new-confirm-dialog.vue";

import BornFreshEmbryo from "@/components/born/fresh-embryo.vue";
import BronFrozenEmbryo from "@/components/born/frozen-embryo.vue";
import BronEmbyroTransfer from "@/components/born/embryo-tranfer.vue";
import { useToast } from "primevue/usetoast";
import { $axios } from "@/service/app-http";

export default {
    components: {
        FertilizationGrid,
        FertilizationEmbryoDetailDialog,
        FertilizationTreeView,
        FertilizationCreateForm,
        FertilizationFollAspirationForm,
        FertilizationEmbryoDetailNewConfirmDialog,
        BornFreshEmbryo,
        BronFrozenEmbryo,
        BronEmbyroTransfer
    },
    setup() {
        const {
            store,
            isBornCycle,
            showGrid,
            problem,
            embryoDetailDialog,
            openEmbryoDetailDialog,
            selectedEmbryo,
            setSelectedEmbryo,
            embryoDetail,
            closeEmbryoDetailDialog,
            getFertilization,
            getEmbryoDetail,
            onCellClick,
            embryoDetailV$,
            embryoDetailSubmitted,
            aspirationForm,
            loadEmbryoDetail
        } = useFertilizationInit();

        const toast = useToast();

        watch(
            () => selectedEmbryo.occId,
            (newVal) => {
                getEmbryoDetail(newVal);
            }
        );

        provide("embryoDetailDialog", embryoDetailDialog);
        provide("openEmbryoDetailDialog", openEmbryoDetailDialog);
        provide("closeEmbryoDetailDialog", closeEmbryoDetailDialog);
        provide("showGrid", showGrid);
        provide("selectedEmbryo", selectedEmbryo);
        provide("setSelectedEmbryo", setSelectedEmbryo);
        provide("embryoDetail", embryoDetail);
        provide("getFertilization", getFertilization);
        provide("getEmbryoDetail", getEmbryoDetail);
        provide("onCellClick", onCellClick);
        provide("embryoDetailV$", embryoDetailV$);
        provide("embryoDetailSubmitted", embryoDetailSubmitted);
        provide("aspirationForm", aspirationForm);

        const embryoDetailCreateConfirmDailog = ref(false);

        const cyclesSummary = computed(
            () => store.state.artCycle.cyclesSummary
        );

        const checkScannedBarcode = async (_barcode) => {
            if (store.state.artCycle) {
                const selectedCycleFert =
                    store.state.artCycle.selectedCycle &&
                    store.state.artCycle.selectedCycle.value.cycleId.toString() ===
                        _barcode
                        ? store.state.artCycle.selectedCycle.value
                        : cyclesSummary.value.find(
                              (row) => row.cycleId.toString() === _barcode
                          );

                // Output the result
                if (selectedCycleFert) {
                    emitter.emit("cycles-ui:play-beep", 1);
                    // Example: Use toast to display the value
                    toast.add({
                        severity: "success",
                        summary: _barcode,
                        detail: "Barcode is correct. Confirmed!",
                        life: 3000
                    });

                    if (
                        store.state.artCycle.selectedCycle.value &&
                        store.state.artCycle.selectedCycle.value.cycleId.toString() ===
                            _barcode
                    ) {
                    } else {
                        store.commit(
                            "artCycle/SET_SELECTED_CYCLE",
                            selectedCycleFert
                        );
                        store.dispatch("born/getBronV2", selectedCycleFert);
                    }
                } else {
                    if (
                        store.state.artCycle.patient &&
                        store.state.artCycle.patient.id == _barcode
                    ) {
                        emitter.emit("cycles-ui:play-beep", 1);
                        toast.add({
                            severity: "warn",
                            summary: _barcode,
                            detail: "Patient is correct. Confirmed!",
                            life: 3000
                        });

                        return;
                    }

                    emitter.emit("cycles-ui:play-beep", 0);
                    toast.add({
                        severity: "error",
                        summary: _barcode,
                        detail: "Barcode is not correct. Wrong!",
                        life: 3000
                    });

                    const scanarea = "fertilization";

                    try {
                        const { data } = await $axios.get(
                            `scan-barcode/data?barcode=${_barcode}&scanarea=${scanarea}`
                        );

                        emitter.emit("cycles-ui:redict-to-other", data);
                    } catch (e) {
                        console.log(e);
                    }
                }
            }
        };

        provide(
            "embryoDetailCreateConfirmDailog",
            embryoDetailCreateConfirmDailog
        );

        const isIVFCycle = store.getters["artCycle/isIVFCycle"];
        const isFETCycle = store.getters["artCycle/isFETCycle"];

        const resizeHandler = (e) => {
            // if (window.innerWidth < 1875) {
            //     splitter.panel1 = 95;
            //     splitter.panel2 = 5;
            // }
        };

        onMounted(() => {
            store.dispatch("provider/getProviders");
            store.dispatch("fertilization/getCycleTypes");
            getFertilization();
            // store.dispatch("fertilization/getOccyteEmbryoDialogOptions");
            store.dispatch("fertilization/getMediaInventoryOptions");
            store.dispatch("cryoStorage/getCryoStorageOptions");
            store.dispatch("fertilization/getAllFreezers");

            emitter.on("cycles-ui:check-barcode", checkScannedBarcode);

            window.addEventListener("resize", resizeHandler);

            emitter.on(
                "embyro-detail-dialog:load-frozen-straw",
                loadEmbryoDetail
            );
        });

        onUnmounted(() => {
            emitter.off("cycles-ui:check-barcode", checkScannedBarcode);
            window.removeEventListener("resize", resizeHandler);

            emitter.off(
                "embyro-detail-dialog:load-frozen-straw",
                loadEmbryoDetail
            );
        });

        return {
            isBornCycle,
            isFETCycle,
            showGrid,
            problem,
            isIVFCycle,
            checkScannedBarcode
        };
    }
};
</script>

<style>
#fertilization .p-component {
    font-size: 0.85rem;
}

.flex-container {
    display: flex;
}

.flex-item {
    flex: 1; /* Takes up remaining space */
    min-width: 0;
}

.fixed-width-item {
    width: 200px;
}

@media (max-width: 768px) {
    .hide-on-mobile {
        display: none;
    }
}
</style>
