<template>
    <div class="p-grid">
        <div class="p-col">
            <div class="p-field p-grid">
                <label class="p-col-fixed" style="width: 120px">
                    Incubators
                </label>
                <div class="p-col-fixed" style="width: 200px">
                    <p-input-text
                        v-model="embryo.incubator"
                        style="width: 100%"
                        v-if="selectedEmbryo.day != 0"
                    />
                    <p-dropdown
                        v-model="embryo.incubator"
                        style="width: 100%"
                        :options="incubatorOptions"
                        v-else
                    />
                </div>
            </div>
            <div class="p-field p-grid">
                <label class="p-col-fixed" style="width: 120px">
                    Media
                </label>
                <div class="p-col-fixed" style="width: 200px">
                    <p-dropdown
                        v-model="embryo.media"
                        :options="mediaOptions"
                        @change="onMedia1Changed"
                        optionLabel="type"
                        optionValue="type"
                        :showClear="true"
                    />
                </div>
            </div>
            <div class="p-field p-grid">
                <label class="p-col-fixed" style="width: 120px">
                    Lot Number
                </label>
                <div class="p-col-fixed" style="width: 200px">
                    <p-input-text
                        v-model="embryo.lotNumber"
                        style="width: 100%"
                    />
                </div>
            </div>
            <div class="p-field p-grid">
                <label class="p-col-fixed" style="width: 120px">
                    Date Opened
                </label>
                <div class="p-col">
                    <p-calendar
                        v-model="embryo.dateOpened"
                        dateFormat="yy-mm-dd"
                        :showIcon="true"
                    />
                </div>
            </div>
            <div class="p-field p-grid p-mt-6">
                <div
                    class="users-signature"
                    v-html="embryo.usersSignature"
                ></div>
            </div>
            <!-- <div class="p-field p-grid p-mt-2">
                <label class="p-mr-1">
                    Updated by:
                </label>
                <label class="p-mr-4"></label>
                <label class="p-mr-1">
                    At:
                </label>
                <label></label>
            </div>
            <div class="p-field p-grid p-mt-2">
                <label class="p-mr-1">
                    Updated by:
                </label>
                <label class="p-mr-4"></label>
                <label class="p-mr-1">
                    At:
                </label>
                <label></label>
            </div> -->
        </div>
        <div class="p-col p-mt-5">
            <div class="p-field p-grid">
                <label class="p-col-fixed" style="width: 120px">
                    Media
                </label>
                <div class="p-col-fixed" style="width: 200px">
                    <p-dropdown
                        v-model="embryo.media2"
                        :options="mediaOptions"
                        @change="onMedia2Changed"
                        optionLabel="type"
                        optionValue="type"
                        :showClear="true"
                    />
                </div>
            </div>
            <div class="p-field p-grid">
                <label class="p-col-fixed" style="width: 120px">
                    Lot Number
                </label>
                <div class="p-col-fixed" style="width: 200px">
                    <p-input-text
                        v-model="embryo.lotNumber2"
                        style="width: 100%"
                    />
                </div>
            </div>
            <div class="p-field p-grid">
                <label class="p-col-fixed" style="width: 120px">
                    Date Opened
                </label>
                <div class="p-col">
                    <p-calendar
                        v-model="embryo.dateOpened2"
                        dateFormat="yy-mm-dd"
                        :showIcon="true"
                    />
                </div>
            </div>
        </div>
        <div class="p-col-fixed" style="width: 200px">
            <img :src="image" alt="image" style="width: 150px; height: 150px" />
        </div>

        <div class="p-col-fixed" style="justify-content: end; display: flex;">
            <table style="max-height: 100px">
                <tr>
                    <td></td>
                    <td>
                        <p-button
                            icon="pi pi-angle-up"
                            @click="onArrowBtnClick('up')"
                        />
                    </td>
                    <td></td>
                </tr>
                <tr>
                    <td>
                        <p-button
                            icon="pi pi-angle-left"
                            @click="onArrowBtnClick('left')"
                        />
                    </td>
                    <td></td>
                    <td>
                        <p-button
                            icon="pi pi-angle-right"
                            @click="onArrowBtnClick('right')"
                        />
                    </td>
                </tr>
                <tr>
                    <td></td>
                    <td>
                        <p-button
                            icon="pi pi-angle-down"
                            @click="onArrowBtnClick('down')"
                        />
                    </td>
                    <td></td>
                </tr>
            </table>
        </div>
    </div>
</template>

<script>
import { computed, inject, onMounted, ref, watch } from "vue";
import { emitter } from "@/event-bus";
import { ORIGIN } from "@/config";
import store from "@/store";
import { TIME_0 } from "@/constants";

export default {
    setup() {
        const embryo = inject("embryoDetail");
        const selectedEmbryo = inject("selectedEmbryo");

        const image = ref("https://via.placeholder.com/300");
        const uploaded = ref(true);
        const imgUploader = (event) => {
            uploaded.value = false;
            const reader = new FileReader();
            reader.readAsDataURL(event.files[0]);
            reader.onload = () => {
                embryo.newImage = reader.result;
                image.value = reader.result;
                uploaded.value = true;
            };
            reader.onerror = (error) => console.log(error);
        };

        const freezerDailog = inject("freezerDailog");

        const mediaOptions = computed(
            () => store.state.fertilization.mediaInventoryList
        );

        // watch(
        //     () => embryo.media,
        //     (val) => {
        //         const foundItem = mediaOptions.value.find(
        //             (item) => item.type === val
        //         );

        //         if (foundItem) {
        //             embryo.lotNumber = foundItem.lotNumber;
        //             embryo.dateOpened = foundItem.startDate
        //                 ? new Date(foundItem.startDate + TIME_0)
        //                 : null;
        //         }
        //     }
        // );

        const onMedia1Changed = () => {
            if (embryo.media) {
                const foundItem = mediaOptions.value.find(
                    (item) => item.type === embryo.media
                );

                if (foundItem) {
                    if (!embryo.lotNumber || embryo.lotNumber === "")
                        embryo.lotNumber = foundItem.lotNumber;

                    if (!embryo.dateOpened || embryo.dateOpened === "")
                        embryo.dateOpened = foundItem.startDate
                            ? new Date(foundItem.startDate + TIME_0)
                            : null;
                }
            }
        };

        const onMedia2Changed = () => {
            if (embryo.media2) {
                const foundItem = mediaOptions.value.find(
                    (item) => item.type === embryo.media2
                );

                if (foundItem) {
                    if (!embryo.lotNumber2 || embryo.lotNumber2 === "")
                        embryo.lotNumber2 = foundItem.lotNumber;

                    if (!embryo.dateOpened2 || embryo.dateOpened2 === "")
                        embryo.dateOpened2 = foundItem.startDate
                            ? new Date(foundItem.startDate + TIME_0)
                            : null;
                }
            }
        };

        const defaultMediaValues = computed(() => {
            return mediaOptions.value
                ? mediaOptions.value.map((option) => option.value)
                : [];
        });

        watch(
            () => embryo.images,
            (val) => {
                if (val.length == 0) {
                    image.value = "https://via.placeholder.com/300";
                } else {
                    image.value = `${ORIGIN}/${val[val.length - 1]}`;
                }
            }
        );

        const onArrowBtnClick = (direction) => {
            emitter.emit("embyro-detail-dialog:move-cell", direction);
        };

        const incubatorOptions = [
            "Minc 1",
            "Minc 2",
            "Minc 3",
            "Planer 1",
            "Planer 2",
            "Planer 3",
            "Planer 4",
            "Ksystem 1",
            "Ksystem 2",
            "Astec"
        ];

        onMounted(() => {
            if (embryo.images.length == 0) {
                image.value = "https://via.placeholder.com/300";
            } else {
                image.value = `${ORIGIN}/${
                    embryo.images[embryo.images.length - 1]
                }`;
            }
        });

        return {
            embryo,
            selectedEmbryo,
            image,
            onArrowBtnClick,
            imgUploader,
            uploaded,
            incubatorOptions,
            mediaOptions,
            onMedia1Changed,
            onMedia2Changed,
            defaultMediaValues,
            showFreezerDialog: () => (freezerDailog.value = true)
        };
    }
};
</script>

<style lang="css" scoped>
.users-signature {
    height: 50px; /* Fixed height */
    overflow-y: auto; /* Enable vertical scrolling */
    padding: 0px 10px; /* Optional: add some padding */
}
</style>
