<template>
    <div style="width: 200px;">
        <p-tree-table
            :value="treeViewData"
            class="p-treetable-sm"
            :scrollable="true"
            scrollHeight="calc(100vh - 165px)"
        >
            <p-column
                field="name"
                style="color: black; background-color: #f8f9fa; font-size: 0.9rem; padding: 2px; padding-bottom: 4px; width: 100px;"
            ></p-column>
            <p-column
                field="value"
                style="color: black; font-weight: bold; font-size: 0.8rem;  padding: 2px; width: 100px;"
            ></p-column>
        </p-tree-table>
        <img
            v-if="image != null"
            :src="image"
            style="width: 200px"
            @click="openImage"
        />
    </div>
</template>

<script>
import { useTreeViewInit } from "@/composable/fertilization";
export default {
    setup() {
        const { treeViewData, image, openImage } = useTreeViewInit();
        return { treeViewData, image, openImage };
    }
};
</script>
